import { useEffect, useState } from "react"
import { aepsWalletTrasfer, aepstransfer } from "../../../api/login/Login"
import {  toast } from "react-toastify"


function AepsWalletTrasfer() {
    const [loader1, setloader1] = useState(false)
    const [transferData, setTransferData] = useState(null)

    const [initialVal, setInitialVal] = useState({
        user_id: '',
        amount: '',
        tpin :'',
    })


    const handleTrasfer = (e) => {
        const name = e.target.name
        const value = e.target.value
    
    // Handle number field
    if (name === 'amount' && (value === '' || (value > 0 && value < 1000000000))) {
        setInitialVal(prevState => ({ ...prevState, [name]: value }));
    }

    // Handle tpin field with length check and allow clearing the input
    if (name === 'tpin') {
        // Only allow digits (0-9) for the tpin field and length check
        const numericValue = value.replace(/[^0-9]/g, ''); // Remove non-numeric characters
        if (numericValue === '' || numericValue.length <= 4) {
            setInitialVal(prevState => ({ ...prevState, [name]: numericValue }));
        }
    }
       
    }


    const aepstTransfer = async () => {
        try {
            const res = await aepstransfer()
            setTransferData(res?.data?.data?.minAepsToMainWallet);
        } catch (error) {

        }
    }

    const toastSuccessMessage = (data) => {
        // console.log(data);
        toast.success(`${data}`, {
            position: "top-center",
            autoClose: 2000,
        });
    };
    const toastWarningMessage = (data) => {
        // console.log(data);
        toast.error(`${data}`, {
            position: "top-center",
            autoClose: 2000,
        });
    };

    const SubmitTransfer = async () => {
        setloader1(true)
        const clone = { ...initialVal, user_id: window.localStorage.getItem("userIdToken") }
        try {
            const res = await aepsWalletTrasfer(clone)
            console.log(res.data.statusCode == "403");
            if (res?.data?.statusCode == "200") {
                toastSuccessMessage(res?.data?.message)
                setInitialVal({amount : '', tpin:''})
               
            }
            if (res?.data?.statusCode == "403") {
                toastWarningMessage(res?.data?.message)
            }
            setloader1(false)
            
        } catch (error) {
            // console.log(error);
            setloader1(false)
        }
    }


    useEffect(() => {
        aepstTransfer()
    }, [])
    return (
        <>
            <div className="PageHeading"><h1>AEPS Wallet to Main Wallet </h1></div>
            <div className="ContentArea"><div className="card">
                <div className="card-header">
                    <span>
                        {/* Sent */}

                    </span>
                    {/* <div style={{ float: 'right', color: '#1e5591', fontWeight: 600, paddingTop: 15 }}>Dhamaka Offer Limit: 62011</div> */}
                </div>
                <div className="card-body">
                    <div className="row row-row" id="dev-search-form">
                        <div className="col-md-12 ">
                            <div className="row">

                          
                            <div className=" col-md-4 form-group mobile-input-container">
                                <label htmlFor="txtAmt">Amount<span id="lenofsendernumber" className="float-right" /></label>
                                <input type="number" id="txtAmt" 
                                autoComplete="off" className="form-control" name="amount" value={initialVal.amount} placeholder="Enter Amount" 
                                 onChange={(e)=>handleTrasfer(e)}
                                />

                               
                                {/* {transferData && <p style={{ color: 'red' }}>Transfer amount must be greater than equal to {transferData}</p>} */}
                            </div>

                            <div className="col-md-4 form-group mobile-input-container">
                                <label htmlFor="ttpin">Tpin<span id="lenofsendernumberpin" className="float-right" /></label>
                              
                                <input type="password" id="ttpin" className="form-control" name="tpin" value={initialVal.tpin} placeholder="Enter tpin" onChange={(e)=>handleTrasfer(e)} autoComplete="off" />
                               
                                {/* {transferData && <p style={{ color: 'red' }}>Transfer amount must be greater than equal to {transferData}</p>} */}
                            </div>
                            </div> 
                           

                           
                        </div>
                        <div className="col-md-12 pt-md-1">
                            <button type="button" id="btnSearch"  
                              className={`btn ${ !initialVal.amount || initialVal.tpin.length !=4 ? 'commonbotton_disable': 'btn-secondary' } custom-button example-1`}
                            disabled={!initialVal.amount || initialVal.tpin.length !=4 } onClick={SubmitTransfer}>
                                Submit
                                {loader1 && <div style={{ height: "16px", width: "16px" }} className="spinner-border" role="status">
                                    <span className="visually-hidden">Loading...</span>
                                </div>
                                }
                            </button>
                        </div>
                    </div>
                </div>
            </div>
            </div>

          
        </>
    )
}
export default AepsWalletTrasfer